import React,{useState, useEffect} from 'react'
import axios from 'axios';
import Compressorstyles from "./compressor.scss";


export default function Compress() {
  const [imageurl, setimageurl] = useState();
  const [compressdImage, setcompressdImage] = useState(null);
  const [downloadImage, setdownloadImage] = useState(null)

  const handleImageChange = (url) => {
    url.preventDefault();

    let imgUrlIn = url.target.value;

    setimageurl(imgUrlIn);

    console.log(imageurl)
    
  };

  const compressImage = () => {

    const apiKey = 'b2118d1b5f824d12a188d6314113151d';

    const dataToSend = {'api_key': apiKey, url: imageurl, quality:50 };

    // const formData = new FormData();
    // formData.append('api_key', apiKey);
    // formData.append('lossy', true);
    // formData.append('image', imageurl);

    axios.post('https://images.abstractapi.com/v1/url/', dataToSend, {
      
      headers: {
        'Content-Type': 'multipart/form-data'
      }

    })
    
    .then(response => {
      console.log(response.data.url);
      let compressedImageResponse = response.data.url;
      setcompressdImage(compressedImageResponse);
      setdownloadImage(compressedImageResponse);

    })
    .catch(error => {
      console.error(error);
    });

  }

  return (
    <>
    <style style={Compressorstyles}></style>
    <section className="compressionBody bg-dark">
      <div className="container bg-dark">
            <div className="row">
                <div className="col-12">
                  <h1 className='mt-5 pt-5 text-light text-center'>
                    Compress images and optimize it
                  </h1>
                  <p className="text-center desc">
                  Compress <b>JPG</b>, <b>PNG</b>, <b>SVG</b> with the best quality and compression. Reduce the filesize of your images at once.
                  </p>
                    <input type="url" name="imageUrl" onChange={handleImageChange} id="imageInputUrl" className="d-block" placeholder="Enter Image URL"/>
                    <button className="btn d-block m-auto mb-3 btn-success" onClick={compressImage}> Compress Image</button>
                    {compressdImage === null ? '' : 
                        <img src={compressdImage} alt="" className='resultImage'/>
                    }
                    
                </div>
            </div>
        </div>
    </section>
    </>
  )
}
